
// 
export function MakeIntComma (intVal){
    intVal *= 1; // 
    const option = {
        maximumFractionDigits: 4 //  
    };
    const result = intVal.toLocaleString('ko-KR', option);
        
    return result
}
// 

// 
export function RemoveIntComma (val){
    val = val+",";
    let regex = /[^0-9]/g;
    let number = val.replace(regex,'');
  
    return number
  }
  // 

//api 
export function errorMsgHandler (error) {  
    let errorMsg = '';
    let errorType = '';

    if(error.hasOwnProperty('message')){
        errorType = "javascript-api";
    }

    if(error.hasOwnProperty('response') && error.response.hasOwnProperty('data')){ //    
        errorType = "custom";
    }
    
    if(error.hasOwnProperty('response') && error.response.hasOwnProperty('data') && error.response.data.hasOwnProperty('error')){ //    
        errorType = "express-validator";
    };

    if(errorType === "javascript-api"){ //    
        errorMsg = error.message;
    }else if(errorType === "custom"){ //    
        errorMsg = error.response.data.msg;
    }else if(errorType === "express-validator"){ // validatior   
        errorMsg = error.response.data.error.errors[0].msg;
    }else{
        errorMsg = '    .';
    }
    return errorMsg
}