import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as CF from "../../config/function";
import { messagePop, filterPop, messagePopList, confirmPop } from "../../store/popupSlice"; 
import SearchBox from "../component/SearchBox";
import SelectBox from "../component/SelectBox";
import MemberListCont from "../component/MemberListCont";
import ConfirmPop from "../popup/ConfirmPop";

const LeftCont = (props) => {
    const popup = useSelector((state)=>state.popup);
    const dispatch = useDispatch();
    const selectList = ["  "," "];
    const selectList2 = [" "," "];
    const [checkNum, setCheckNum] = useState(0);
    const [confirm, setConfirm] = useState(false);

    // Confirm 
    useEffect(()=>{
        if(popup.confirmPop === false){
            setConfirm(false);
        }
    },[popup.confirmPop]);

    //   
    useEffect(()=>{
        const num = popup.messagePopList.length;
        setCheckNum(num);
    },[popup.messagePopList]);

    //  
    const groupMsgSend = () => {
        if(checkNum > 0){
            dispatch(messagePop(true));
        }else{
            dispatch(confirmPop({
                confirmPop:true,
                confirmPopTit:'',
                confirmPopTxt: '  .',
                confirmPopBtn:1,
            }));
            setConfirm(true);
        }
    };


    return(<>
        <div className="left_cont">
            <div className="top_box">
                <div className="tit_box flex w_100">
                    <h1>{props.page === "member" ? "" : props.page === "message" ? "" : props.page === "chat" ? " " : props.page === "setting" && ""}</h1>
                    {props.page !== "setting" &&
                        <p className="flex">{props.page === "member" ? "  " : props.page === "message" ? "  " : props.page === "chat" && "  "}
                        <span><strong>{CF.MakeIntComma(props.allCount)}</strong> {props.page === "member" ? "" : props.page === "message" ? "" : props.page === "chat" && ""}</span></p>
                    }
                </div>
                {props.page === "member" || props.page === "message" ? //,  
                    <div className="round_box flex_between w_100">
                        <div className="flex">
                            <h6></h6>
                            <p className="txt flex">  <span><strong>{CF.MakeIntComma(checkNum)}</strong> </span></p>
                        </div>
                        <button className="btn_send" onClick={groupMsgSend}></button>
                    </div>
                    : props.page === "chat" && // 
                    <div className="round_box round_box2 flex_between w_100">
                        <ul className="flex_between w_100">
                            <li className="flex_between">
                                <p> </p>
                                <p><strong>62</strong> </p>
                            </li>
                            <li className="flex_between">
                                <p> </p>
                                <p><strong>1,500</strong> </p>
                            </li>
                        </ul>
                    </div>
                }
            </div>
            <div className="bottom_box">
                {props.page !== "setting" ?
                    <>
                        <div className="top_search">
                            <SearchBox 
                                placeholder=" "
                                searchValue={props.searchValue}
                                onChangeHandler={props.onSearchChange}
                                onSearchHandler={props.onSearchHandler}
                            />
                            <div className="flex_between tp8">
                                <p className="txt">{` ${props.page == "chat" ? "" : ""}`} <span><strong>{CF.MakeIntComma(props.listCount)}</strong> {props.page == "chat" ? "" : ""}</span></p>
                                <div className="flex">
                                    <SelectBox 
                                        list={props.page === "chat" ? selectList2 : selectList}
                                        selected={props.listSelected}
                                        onChangeHandler={props.onSelChange}
                                        selHidden={props.selHidden}
                                    />
                                    {props.page === "member" &&
                                        <button className="btn_filter" onClick={()=>{dispatch(filterPop(true))}}></button>
                                    }
                                </div>
                            </div>
                        </div>
                        <MemberListCont
                            allCheck={props.allCheck}
                            list={props.list}
                            listType={props.listType}
                        />
                    </>
                    :   <div className="setting_box">
                            <div className="scroll_wrap">
                                <p className="tit"> </p>
                                <ul className="select_ul">
                                    <li className="flex_between">
                                        <p>  </p>
                                        <SelectBox 
                                            list={["100","200","300","400","500","600","700","800","900","1000"]}
                                            selected={props.selectedNum}
                                            onChangeHandler={props.onSelNumChange}
                                            listType={"number"}
                                        />
                                    </li>
                                    <li className="flex_between">
                                        <p>  </p>
                                        <SelectBox 
                                            list={["0~1","2~3","4~6","7~9"]}
                                            selected={props.selectedRange}
                                            onChangeHandler={props.onSelRangeChange}
                                        />
                                    </li>
                                </ul>
                            </div>
                            <button type="button" className="btn" onClick={props.onSaveHandler}> </button>
                        </div>
                }
            </div>
        </div>

        {/* confirm */}
        {confirm && <ConfirmPop />}
    </>);
};

export default LeftCont;