import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import * as CF from "../../config/function";
import { enum_api_uri } from "../../config/enum";
import { messagePop, memCheckPop, confirmPop, loadingPop } from "../../store/popupSlice";
import { groupMsg } from "../../store/commonSlice";
import ConfirmPop from "./ConfirmPop";
import MemberListCont from "../component/MemberListCont";
import MessageInputWrap from "../component/MessageInputWrap";

const MessagePop = (props) => {
    const popup = useSelector((state)=>state.popup);
    const common = useSelector((state)=>state.common);
    const dispatch = useDispatch();
    const token = localStorage.getItem("token");
    const g_msg_list = enum_api_uri.g_msg_list;
    const g_msg_list_add = enum_api_uri.g_msg_list_add;
    const g_msg_send = enum_api_uri.g_msg_send;
    const g_msg_img_send = enum_api_uri.g_msg_img_send;
    const [confirm, setConfirm] = useState(false);
    const [closeConfirm, setCloseConfirm] = useState(false);
    const [sendConfirm, setSendConfirm] = useState(false);
    const [sendOkConfirm, setSendOkConfirm] = useState(false);
    const [list, setList] = useState([]);

    const [textareaValue, setTextareaValue] = useState("");

    //
    const closePopHandler = () => {
        dispatch(messagePop(false));
    };

    // Confirm 
    useEffect(()=>{
        if(popup.confirmPop === false){
            setConfirm(false);
            setCloseConfirm(false);
            setSendConfirm(false);
            setSendOkConfirm(false);
        }
    },[popup.confirmPop]);


    //   
    const getList = () => {
        dispatch(loadingPop(true));

        let body = {
            to_id: popup.messagePopList
        };

        axios.post(`${g_msg_list}`,body,
            {headers: {Authorization: `Bearer ${token}`}}
        )
        .then((res)=>{
            if(res.status === 200){
                dispatch(loadingPop(false));

                let data = res.data;
                setList([...data]);
            }
        })
        .catch((error) => {
            dispatch(loadingPop(false));
            
            const err_msg = CF.errorMsgHandler(error);
            dispatch(confirmPop({
                confirmPop:true,
                confirmPopTit:'',
                confirmPopTxt: err_msg,
                confirmPopBtn:1,
            }));
            setConfirm(true);
        });
    };


    useEffect(()=>{
        getList();
    },[popup.messagePopList]);


    //  
    const addHandler = () => {
        dispatch(loadingPop(true));

        let body = {
            to_id: popup.messagePopList
        };

        axios.post(`${g_msg_list_add}`,body,
            {headers: {Authorization: `Bearer ${token}`}}
        )
        .then((res)=>{
            if(res.status === 200){
                dispatch(loadingPop(false));

                let data = res.data;
                dispatch(memCheckPop({memCheckPop:true,memCheckPopTit:"",memCheckPopList:data}));
            }
        })
        .catch((error) => {
            dispatch(loadingPop(false));
            
            const err_msg = CF.errorMsgHandler(error);
            dispatch(confirmPop({
                confirmPop:true,
                confirmPopTit:'',
                confirmPopTxt: err_msg,
                confirmPopBtn:1,
            }));
            setConfirm(true);
        });
    };


    // 
    const imgAttach = () => {
        let body = {
            to_id: popup.messagePopList,
            image_array: common.msgImgs
        };

        axios.post(`${g_msg_img_send}`,body,
            {headers: {Authorization: `Bearer ${token}`}}
        )
        .then((res) => {
            if (res.status === 200) {
                dispatch(confirmPop({
                confirmPop:true,
                confirmPopTit:'',
                    confirmPopTxt: "   !",
                    confirmPopBtn:1,
                }));
                setSendOkConfirm(true);

                dispatch(groupMsg(true));
            }
        })
        .catch((error) => {
            const err_msg = CF.errorMsgHandler(error);
            dispatch(confirmPop({
                    confirmPop:true,
                    confirmPopTit:'',
                confirmPopTxt: err_msg,
                confirmPopBtn:1,
            }));
            setConfirm(true);
        });
    };


    // 
    const textSend = () => {
        let body = {
            to_id: popup.messagePopList,
            msg: textareaValue
        };

        axios.post(`${g_msg_send}`,body,
            {headers: {Authorization: `Bearer ${token}`}}
        )
        .then((res) => {
            if (res.status === 200) {
                dispatch(confirmPop({
                    confirmPop:true,
                    confirmPopTit:'',
                    confirmPopTxt: "   !",
                    confirmPopBtn:1,
                }));
                setSendOkConfirm(true);

                dispatch(groupMsg(true));
            }
        })
        .catch((error) => {
            const err_msg = CF.errorMsgHandler(error);
            dispatch(confirmPop({
                confirmPop:true,
                confirmPopTit:'',
                confirmPopTxt: err_msg,
                confirmPopBtn:1,
            }));
            setConfirm(true);
        });
    };

   
    //  
    const msgSendHandler = () => {
        dispatch(confirmPop({
            confirmPop:true,
            confirmPopTit:'',
            confirmPopTxt: "   ?",
            confirmPopBtn:2,
        }));
        setSendConfirm(true);
    };

    // 
    const sendHandler = () => {
        if(textareaValue){
            textSend();
        }
        if(common.msgImgs.length > 0){
            imgAttach();
        }
    };


    //,  
    const cancelHandler = () => {
        dispatch(confirmPop({
            confirmPop:true,
            confirmPopTit:'',
            confirmPopTxt: "    ?",
            confirmPopBtn:2,
        }));
        setCloseConfirm(true);
    };


    return(<>
        <div className="pop_wrap message_pop">
            <div className="dim"></div>
            <div className="pop_cont pop_cont3">
                <div className="pop_tit flex_between">
                    <p className="f_24"><strong> </strong></p>
                    <button type="button" className="btn_close" onClick={cancelHandler}></button>
                </div>
                <div className="list_cont">
                    <div className="top_box flex_between">
                        <div className="tit flex"><strong> </strong><span><strong>{CF.MakeIntComma(list.length)}</strong> </span></div>
                        <div className="flex">
                            <button type="button" className="btn_round3 rm4" onClick={()=>{
                                dispatch(memCheckPop({memCheckPop:true,memCheckPopTit:"",memCheckPopList:list}));
                            }}></button>
                            <button type="button" className="btn_round2" onClick={addHandler}></button>
                        </div>
                    </div>
                    <MemberListCont
                        list={list}
                        listType="member"
                    />
                </div>
                <div>
                    <MessageInputWrap 
                        textareaValue={textareaValue}
                        onTextareaChange={(e)=>{setTextareaValue(e.currentTarget.value)}}
                        onMsgSendHandler={msgSendHandler}
                        group={true}
                    />
                </div>
            </div>
        </div>

        {/*   confirm */}
        {sendConfirm && <ConfirmPop onClickHandler={sendHandler} />}

        {/*    confirm */}
        {sendOkConfirm && <ConfirmPop closePop="custom" onCloseHandler={closePopHandler} />}

        {/*   confirm */}
        {closeConfirm && <ConfirmPop onClickHandler={closePopHandler} />}

        {/* confirm */}
        {confirm && <ConfirmPop />}
    </>);
};

export default MessagePop;